.arrow_back,
.arrow_forward {
  display: inline-block;
  position: relative;
  top: -2px;
  padding: 4px 0 0;
  vertical-align: middle;
}
.arrow_back:before,
.arrow_forward:before,
.arrow_back:after,
.arrow_forward:after {
  display: block;
  content: '';
  transition: background 0.15s linear, border 0.15s linear;
}
.arrow_back:before,
.arrow_forward:before {
  height: 2px;
  width: 15px;
  background: #FAAD16;
}
.arrow_back:after,
.arrow_forward:after {
  position: absolute;
  height: 10px;
  width: 10px;
  top: 0;
  border: 2px solid #FAAD16;
}
a:hover .arrow_back:before,
a:hover .arrow_forward:before {
  background: #005DB8;
}
a:hover .arrow_back:after,
a:hover .arrow_forward:after {
  border-color: #005DB8;
}
.arrow_back {
  margin: 0 10px 0 0;
}
.arrow_back:after {
  left: 0;
  border-width: 2px 0 0 2px;
  transform: rotate(-45deg);
}
.arrow_forward {
  margin: 0 0 0 10px;
}
.arrow_forward:after {
  right: 0;
  border-width: 2px 2px 0 0;
  transform: rotate(45deg);
}
