.header .main_nav li ul {
  position: absolute;
  width: 250px;
  top: 100%;
  left: 50%;
  z-index: 2;
  background: #333333;
  box-shadow: 0 2px 5px rgba(18, 18, 18, 0.15);
  opacity: 0;
  margin: 0 0 0 -125px;
  padding: 15px;
  text-align: left;
  visibility: hidden;
  transition: opacity 0.1s linear, transform 0.1s linear, visibility 0.1s linear;
  transform: scale(0.95) translateY(-3px);
  transform-origin: center top;
}
.header .main_nav li ul:before {
  position: absolute;
  height: 1px;
  width: 1px;
  top: -6px;
  right: 0;
  left: 0;
  border-bottom: 6px solid rgba(18, 18, 18, 0.8);
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
