.mobile_nav_tray {
  display: none;
}
.mobile_nav_tray,
.mobile_nav_tray.fs-navigation-enabled {
  width: 100%;
  background: #002C73;
  box-shadow: none;
}
.mobile_nav_tray,
.mobile_nav_tray.fs-navigation-enabled,
.mobile_nav_tray .mobile_nav_container {
  transition: transform 0.5s ease;
  transform: translate3D(101%, 0, 0);
}
.mobile_nav_tray .mobile_nav_container {
  position: absolute;
  display: flex;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #005DB8;
  flex-direction: column;
  justify-content: space-between;
}
.mobile_nav_tray.fs-navigation-open {
  transform: translate3D(0, 0, 0);
}
.mobile_nav_tray.fs-navigation-open .mobile_nav_container {
  transform: translate3D(10px, 0, 0);
}
@media only screen and (min-width: 740px ) {
  .mobile_nav_tray.fs-navigation-open .mobile_nav_container {
    transform: translate3D(20px, 0, 0);
  }
}
.mobile_nav_tray.fs-navigation-open .main_nav li {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}
.mobile_nav_tray.fs-navigation-open .main_nav li:nth-child(20) {
  transition-delay: 1.4s;
}
.mobile_nav_tray.fs-navigation-open .main_nav li:nth-child(19) {
  transition-delay: 1.35s;
}
.mobile_nav_tray.fs-navigation-open .main_nav li:nth-child(18) {
  transition-delay: 1.3s;
}
.mobile_nav_tray.fs-navigation-open .main_nav li:nth-child(17) {
  transition-delay: 1.25s;
}
.mobile_nav_tray.fs-navigation-open .main_nav li:nth-child(16) {
  transition-delay: 1.2s;
}
.mobile_nav_tray.fs-navigation-open .main_nav li:nth-child(15) {
  transition-delay: 1.15s;
}
.mobile_nav_tray.fs-navigation-open .main_nav li:nth-child(14) {
  transition-delay: 1.1s;
}
.mobile_nav_tray.fs-navigation-open .main_nav li:nth-child(13) {
  transition-delay: 1.05s;
}
.mobile_nav_tray.fs-navigation-open .main_nav li:nth-child(12) {
  transition-delay: 1s;
}
.mobile_nav_tray.fs-navigation-open .main_nav li:nth-child(11) {
  transition-delay: 0.95s;
}
.mobile_nav_tray.fs-navigation-open .main_nav li:nth-child(10) {
