.subnav_container {
}

.subnav_handle {
  display: none;

  &.fs-navigation-enabled {
    .block;
    .avenir_black;

    color: @blue_mid;
    font-size: 16px;
    padding: 15px 20px;
    text-align: left;
    text-transform: uppercase;

    @media @max_lg {
      border: 1px solid @gray_20;
    }
  }

  span {
    .inline_block;
    .relative;

    height: 2px;
    width: 18px;

    top: -1px;

    background: @blue_mid;
    box-shadow:
      0 5px 0 @blue_mid,
      0 -5px 0 @blue_mid;
    content: '';
    margin: 0 10px 0 0;
    vertical-align: middle;
    transition:
      background 0.15s linear,
      box-shadow 0.15s linear;
  }
}

.subnav {

  @media @max_lg {
    border: 1px solid @gray_20;
    border-width: 0 1px 1px;
    margin: 0 0 30px;
    padding: 20px;
  }

  @media @min_md {
    // padding: 0 20px 30px;
  }

  @media @min_lg {
    // padding: 30px;
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  li {
  }

  //

  .sub-menu {
    display: none;

    li {
      border-bottom: 1px solid @gray_20;

      &:last-child {
        border: none;
      }
    }

    .sub-menu {
      border-top: 1px solid @gray_20;
      padding-left: 20px;

      a {
        color: @gray_80;

        &:hover {
          color: @blue_dark;
        }
      }

      .current-menu-item,
      .current-cat {

        > a {
          .avenir_black;
        }
      }

      .sub-menu {
        padding: 5px 0 5px 20px;

        li {
          border: none;
        }

        a {
          font-size: 16px;
          padding: 5px 0;
        }

        .current-menu-item,
        .current-cat {

          &:before {
            top: 8px;
            left: -25px;

            border-width: 0 12px 12px 0;
          }
        }
      }
    }
  }

  .current-menu-ancestor > .sub-menu,
  .current-menu-item > .sub-menu {
    display: block;
  }

  //

  .menu > li {
    display: none;

    &.current-menu-item,
    &.current-menu-ancestor {
      .block;

      > a {
        display: none;
      }
    }
  }

  a {
    .block;
    .avenir;

    color: @blue_mid;
    font-size: 18px;
    padding: 10px 0;

    // @media @max_lg {
    //   padding-left: 50px;
    // }

    @media @min_md {
      font-size: 20px;
    }

    &:hover,
    &:focus {
      color: @blue_dark;
    }
  }

  .current-menu-item,
  .current-cat {
    .relative;

    > a {
      .avenir_black;
    }

    &:before {
      .block;
      .absolute;

      top: 10px;
      left: -30px;

      border-bottom: 16px solid transparent;
      border-right: 16px solid @yellow_mid;
      content: '';

      @media @max_lg {
        display: none;
      }

      @media @min_md {
        top: 15px;
      }
    }
  }
}


