.newsletter_form {

  @media @min_md {

    .gform_body,
    .gform_footer {
      width: 48%;
    }

    .gform_body {
      .left;

      clear: none;

      ul {
        column-count: 1;
      }
    }

    .gform_footer {
      .right;

      clear: none;
      margin: 0;

      input[type='submit'],
      button.button,
      .gform_page_footer input.button {
        width: 100%;
      }
    }
  }
}



//

.page_content {

  .gform_wrapper {
    background: @blue_mid;
    padding: 40px 30px;
    margin: 20px -10px;

    @media @min_md {
      margin: 30px 0;
      padding: 40px 30px;
    }

    @media @min_lg {
      padding: 50px 40px;
    }

    ul {

      li {

        &:before {
          display: none;
        }
      }
    }
  }

  .gform_title {
    &:extend(.form_block_title all);
  }
}
