.main_nav {
  .right;

  @media @max_xl {
    display: none;
  }

  @media @min_xl {
    margin: 45px 0 0;
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 0;

    li {
      .left;

      margin: 0 0 0 5px;
      padding: 0;

      @media @min_xl {
        margin: 0 0 0 10px;
      }
    }

    a {
      .block;
      .avenir;

      color: @gray_60;
      font-size: 16px;
      padding: 10px;

      @media @min_xl {
        font-size: 18px;
      }

      &:hover,
      &:focus {
        color: @blue_mid;
      }
    }

    .button {
      margin-left: 20px;

      a {
        .button_blue;

        padding: 17px 30px 16px;

        @media @min_xl {
          padding: 17px 30px 16px;
        }
      }
    }
  }
}

.header {

  .main_nav {

    li {
      .relative;

      & ul {
        .absolute;

        width: 250px;

        top: 100%;
        left: 50%;
        z-index: 2;

        background: @gray_80;
        box-shadow: 0 2px 5px fade(@black, 15);
        opacity: 0;
        margin: 0 0 0 -125px;
        padding: 15px;
        text-align: left;
        visibility: hidden;
        transition:
          opacity 0.1s linear,
          transform 0.1s linear,
          visibility 0.1s linear;
        transform: scale(0.95) translateY(-3px);
        transform-origin: center top;

        &:before {
          .absolute;

          height: 1px;
          width: 1px;

          top: -6px;
          right: 0;
          left: 0;

          border-bottom: 6px solid fade(@black, 80);
          border-right: 6px solid transparent;
          border-left: 6px solid transparent;
          content: '';
          margin: 0 auto;
        }
      }

      li {
        .block;

        float: none;
        margin: 0;
        padding: 0;

        a {
          .avenir;

          color: @white;
          font-size: 16px;
          padding: 5px;

          &:hover {
            color: @blue_lighter;
          }
        }
      }

      &:hover {

        ul {
          opacity: 1;
          transform: scale(1) translateY(0);
          visibility: visible;
        }
      }
    }
  }
}
